namespace $ { export class $mol_app_taxon extends $mol_pager {

	/// hierarhyField \Butxt
	hierarhyField() {
		return "Butxt"
	}

	/// records /
	records() {
		return [] as any[]
	}

	/// record# null
	record( key : any ) {
		return <any> null
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// rowExpanded# false
	@ $mol_mem_key()
	rowExpanded( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// rowLevel# 0
	rowLevel( key : any ) {
		return 0
	}

	/// grider $mol_grider 
	/// 	hierarhyColumn < hierarhyField 
	/// 	records < records 
	/// 	record# < record# 
	/// 	rows < rows 
	/// 	rowExpanded# > rowExpanded# 
	/// 	rowLevel# < rowLevel#
	@ $mol_mem()
	grider( next? : any ) {
		return new $mol_grider().setup( obj => { 
			obj.hierarhyColumn = () => this.hierarhyField()
			obj.records = () => this.records()
			obj.record = ( key : any ) => this.record( key )
			obj.rows = () => this.rows()
			obj.rowExpanded = ( key : any , next? : any ) => this.rowExpanded( key , next )
			obj.rowLevel = ( key : any ) => this.rowLevel( key )
		} )
	}

	/// bodier < grider
	bodier() {
		return this.grider()
	}

} }

